:root{
  --font:#fff;
  --border:rgb(66, 66, 66);
}
.addList{
  display: flex;
  position: relative;
}
.addList>input{
  padding: 0 10px;
  height: 45px;
  border-radius: 5px;
  border: 2px solid var(--border);
  caret-color: #000;
  font-size: 14px;
}
.addList>input:focus{
  border: 2px solid #000;
  outline: none;
}
.addList>input:hover{
  border: 2px solid #000;
}
.addList>svg{
  width: 30px;
  cursor: pointer;
  background-color: rgb(171 171 171 / 86%);
  position: absolute;
  right: 10px;
  top: 10px;
  border-radius: 5px;
}
.addList>svg>path{
  fill: white;
}
.addList>svg:hover{
  background-color: rgb(171 171 171);
}
.alert{
  margin: 5px 0 10px 0 ;
  color: rgb(189, 16, 16);
  display: flex;
}
.alert>svg{
  fill: rgb(189, 16, 16);
}
.alert>span{
  margin: 0 5px;
  font-weight: bold;
}
@media (max-width:768px){
  .addList>input{
    width: 100%;
  }
}